import axios from "axios";
import store from "../store";
import router from "../router";
import { Message, MessageBox, Loading } from "element-ui";

let loadingInstance = null; // ÂÖ®Â±è Loading ÁöÑÂÆû‰æã

/*
 * ÂàõÂª∫ axios ÂÆû‰æã
 * Áä∂ÊÄÅÁ†Å
 * Ëá™ÂÆö‰πâÈªòËÆ§ÂÄº
 * Ë∂ÖÊó∂timeout
 * withCredentials ÊòØÂê¶Ë∑®Âüü
 *baseURL
 * */
const instance = axios.create({
  // baseURL: "http://localhost:3000/",
  // withCredentials: true, //
  // // `validateStatus` defines whether to resolve or reject the promise for a given
  // // HTTP response status code. If `validateStatus` returns `true` (or is set to `null`
  // // or `undefined`), the promise will be resolved; otherwise, the promise will be
  // // rejected.
  // validateStatus: function (status) {
  //   return status >= 200 && status < 300; // default
  // },
  timeout: 10000
});
/*
 * ËØ∑Ê±ÇÊã¶Êà™Âô®
 * ËÆæÁΩÆ‰∏Ä‰∏™ loading ÔºåÊØèÊ¨°ËØ∑Ê±Ç‰πãÂâçÊòæÁ§∫
 *
 * */
instance.interceptors.request.use(
  config => {
    // Do something before request is sent
    /*
     * ËØ∑Ê±ÇÂèëÈÄÅ‰πãÂâç
     * */
    /*
     * ÊòæÁ§∫ loading
     * */
    loadingInstance = Loading.service({ lock: true, text: "ÊãºÂëΩÂä†ËΩΩ‰∏≠" });
    if (store.state.AUTH_TOKEN) {
      // ÊòØÂê¶ Â≠òÂú®token
      config.headers.Authorization = store.state.AUTH_TOKEN;
    }
    return config;
  },
  error => {
    /*
     * ÂÖ≥Èó≠ loading
     * */
    loadingInstance.close();
    // Do something with request error
    /*
     * ÂìçÂ∫îÈîôËØØ
     * */
    // console.warn(error);
    return Promise.reject(error);
  }
);
/*
 * ÂìçÂ∫îÊã¶Êà™Âô®
 * ÂÖ≥Èó≠ loading
 * */
instance.interceptors.response.use(
  response => {
    /*
     * ÂÖ≥Èó≠ loading
     * */
    loadingInstance.close();
    // Any status code that lie within the range of 2xx cause this function to trigger
    // Do something with response data
    /*
     *  status >= 200 && status < 300;
     * ÊàêÂäüÁä∂ÊÄÅÁ†Å
     * */
    // console.log(response);
    return response.status === 200 ? Promise.resolve(response) : Promise.reject(response);
  },
  error => {
    /*
     * ÂÖ≥Èó≠ loading
     * */
    loadingInstance.close();
    /*
     *  status < 200 || status >= 300;
     * Â§±Ë¥•Áä∂ÊÄÅÁ†Å
     * */
    const { response } = error;
    if (response) {
      // console.log(response);
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      // console.log(response.data);
      // console.log(response.status);
      // console.log(response.headers);
      // console.log(response.statusText);
      switch (response.status) {
        case 400:
          MessageBox.confirm("ÂÆ¢Êà∑Á´ØËØ∑Ê±ÇÁöÑËØ≠Ê≥ïÈîôËØØÔºåÊúçÂä°Âô®Êó†Ê≥ïÁêÜËß£", "Error");
          break;
        case 401:
          // ËøîÂõû 401 Ê∏ÖÈô§token‰ø°ÊÅØÂπ∂Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
          store.commit("removeToken");
          if (router.currentRoute.path !== "login") {
            router.replace({
              path: "login",
              query: { redirect: router.currentRoute.fullPath }
            });
          }
          // MessageBox.confirm("Ë¶ÅÊ±ÇÁî®Êà∑ÁöÑË∫´‰ªΩËÆ§ËØÅ", 'Error');
          break;
        case 403:
          MessageBox.confirm("ÁôªÂΩïËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï", "Error");
          break;
        case 404:
          Message.warning(response.statusText);
          break;
        default:
          Message({
            type: "error",
            message: response.statusText
          });
          // return Promise.reject(error.response);
          break;
      }

      // } else if (error.request) {
      //   // The request was made but no response was received
      //   // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
      //   // http.ClientRequest in node.js
      //   console.log(error.request);
      //   console.log('Error', error.message);
    } else {
      // Something happened in setting up the request that triggered an Error
      // console.log('Error', error.message);
      // MessageBox.confirm(message, title, options)
      MessageBox.confirm(error.message, "Error", {
        distinguishCancelAndClose: true,
        confirmButtonText: "Âà∑Êñ∞ÈáçËØï",
        cancelButtonText: "ËøîÂõûÈ¶ñÈ°µ",
        type: "warning"
      })
        .then(() => {
          router.go(0);
          Message({
            type: "success",
            message: "Âà∑Êñ∞ÊàêÂäü!"
          });
        })
        .catch(action => {
          if (action === "cancel") {
            router.push("/");
          }
        });
      // console.log(error.config);
      return Promise.reject(error);
    }
  }
);

export default instance;
