<template>
  <el-form status-icon :model="myData" :rules="myRules" ref="myForm" label-width="100px" class="myForm">
    <el-form-item label="ÂØÜÁ†Å" prop="pass">
      <el-input v-model="myData.pass" autocomplete="off" type="password"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass" label="Á°ÆËÆ§ÂØÜÁ†Å">
      <el-input v-model="myData.checkPass" type="password" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item prop="age" label="Âπ¥ÈæÑ">
      <el-input autocomplete="off" v-model.number="myData.age"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('myForm')">Êèê‰∫§</el-button>
      <el-button @click="resetForm('myForm')">ÈáçÁΩÆ</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "TheLogin",
  data() {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("Âπ¥ÈæÑ‰∏çËÉΩ‰∏∫Á©∫"));
      }
      // setTimeout(()=>{},1000); Â±ïÁ§∫È™åËØÅËøáÁ®ãÔºåËßÜËßâÊïàÊûúÂ•Ω
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          return callback(new Error("ËØ∑ËæìÂÖ•Êï¥Êï∞"));
        } else {
          if (value < 18) {
            return callback(new Error("ÂøÖÈ°ªÂπ¥Êª°18Âë®Â≤Å"));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("ËØ∑ËæìÂÖ•ÂØÜÁ†Å"));
      } else {
        // Âè¶‰∏Ä‰∏™Â∑≤ËæìÂÖ•Ôºå‰øÆÊîπÊú¨Ë∫´ÔºåÂú®È™åËØÅ
        if (this.myData.checkPass !== "") {
          this.$refs.myForm.validateField("checkPass");
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å"));
      } else if (value !== this.myData.pass) {
        return callback(new Error("‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥"));
      } else {
        callback();
      }
    };
    return {
      myData: {
        pass: "",
        checkPass: "",
        age: ""
      },
      myRules: {
        pass: [{ trigger: "blur", validator: validatePass }],
        checkPass: [{ trigger: "blur", validator: validatePass2 }],
        age: [{ trigger: "blur", validator: checkAge }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const params = {
            pass: this.myData.pass,
            checkPass: this.myData.checkPass,
            age: this.myData.age
          };
          this.$ajax.application
            .login(params)
            .then(res => {
              console.log("res");
              if (res.data) {
                console.log(res);
                if (res.data.token) {
                  this.$store.commit("setToken", res.data.token);
                }
                console.log(this.$route);
                const redirectUrl = decodeURIComponent(this.$route.query.redirect || "/");
                console.log(redirectUrl);
                this.$store.commit("setActiveIndex", redirectUrl);
                this.$router.push(redirectUrl);
              } else {
                console.log("then error");
                this.$message({
                  type: "error",
                  message: res.statusText
                });
              }
              console.log("res");
            })
            .catch(error => {
              console.log("catch error");
              console.warn(error);
              this.$message({
                type: "success",
                message: "Âà∑Êñ∞ÊàêÂäü!"
              });
              // this.$message({
              //   type: "error",
              //   message: error.statusText
              // });
            });
          // alert("submit")
          // } else {
          //     console.warn("error");
          //     this.$message({
          //         type: 'error',
          //         message: response.statusText
          //     });
          //     return false
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
.myForm {
  width: 460px;
  margin: 40px auto 0;
}
</style>
